.modal-content {
    position: absolute;
    top: 60%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    padding: 0;
    border: none;
    
  }
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  
    overflow: hidden;
    overflow-y: auto;
    z-index: 999;
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  .ReactModal__Overlay {  
    background-color: rgba(18, 20, 23, 0.5);
  }
  
  .ReactModal__Overlay--after-open {
    opacity: 1;
  }
  
  .ReactModal__Overlay--before-close {
    opacity: 0;
  }
